.full-recipe {
    background-color: rgb(250, 245, 245);
    overflow: auto;
    max-height: 80vh;
    color: #666;
    display: grid;
    grid-template-columns: minmax(250px, 1fr);
    grid-auto-rows: min-content;
}

.recipeImage-container {
    overflow: hidden;
    position: relative;
}

.score {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(60, 60, 60, .5);
    height: min-content;
    padding: .75rem;
    color: white;
    width: min-content;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.7rem;
}
.icon-star{
    font-size: 3rem;
}


.recipeImage-container img {
    display: block;
    object-fit: cover;
    width: 100%;
    // height: 45vh;
}


.heading {
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: rgba(60, 60, 60, .75);
    border-radius: 20px 0 0 0;
    padding: .5rem 2rem;
    width: 90%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.icon-heart {
    font-size: 3rem;
    color: rgba(255 255 255 / 20%);
}


.recipe-label {
    font-size: 1.8rem;
    color: white;
    font-weight: 400;
    margin-left: auto;
    text-align: right;
}




.alergens {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-content: center;
    flex-wrap: wrap;
    padding: 3px 0;
    font-size: 1rem;
    // background-color: orange; 
}

.alergens li {
    text-transform: capitalize;
    display: flex;
    align-items: center;
}
.icon-alergen{
    margin-left: .5rem;
    font-size: 1.2rem;
}

.recipe-insights {
    display: flex;
    // align-items: center;
    justify-content: space-evenly;
    margin: 2rem 1rem;

}

.insight {
    // background-color: hsla(76, 81%, 61%, 0.1);
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem;
    font-size: 1.8rem;
}

.icon-insight {
    font-size: 3.5rem;
}


//quantity
.servings {
    display: flex;
    align-items: center;
    margin-left: 5%;
    font-size: 1.6rem;
}
.servings strong{
    margin-right: 1rem;
}

.controls {
    // background-color: lightblue;
    flex: 0 1 12rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    
}

.decrease-btn,
.increase-btn {
    border: none;
    background-color: inherit;
    background-color: rgba(1,1,1, 0.1);
    padding: 1rem;
    border-radius: 100px;
    box-shadow: inset 2px 2px 2px rgba(50,50,50,0.1),
                inset -2px -2px 8px 5px rgba(200,200,200,0.7),
                       2px  2px 5px rgba(100,200,220,0.5);
    cursor: pointer;

}


.servings-qty {
    font-size: 1.8rem;
    color: rgb(27, 122, 247);
    margin: 0 2rem;
}

.icon-qty{
    font-size: 1rem;
    pointer-events: none;
}



.ingredients-list {
    list-style: none;
    padding: 0 5%;
    margin: 3rem 5%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    font-size: 1.7rem;
}

.ingredient {
    flex: 1 1;
    color: rgb(49, 48, 48);
    font-style: italic;
}

.ingredient:before {
    content: '\2713';
    margin-right: 6px;
    font-size: 1.8rem;
    color: red;
    // background: pink;

}
.ingredient-amount{
    font-weight: bold;
}

.more {
    margin: 2.5%;
    padding: 1.5rem;
    font-size: 1.5rem;
    color: #222;
    border-radius: .5rem;
    
}

.instructions {
    background-color: rgba(75, 240, 75, 0.1);
    position: relative;
    &__steps li{
        display: flex;
        justify-content: flex-start;
        // flex-direction: column;
        margin: 0;
    }

    &__steps .num_step {
        min-height: 4rem;
        max-height: 4rem;
        min-width: 4rem;
        background-color: rgba(75, 240, 75, 0.5);
        // background-color: black;
        margin-right: 1rem;
        color: white;
        font-size: 1.8rem;
        font-weight: 600;
        border: 3px solid rgba(75, 240, 75, 0.5);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    

    &__steps ul,
    &__steps ol {
        list-style: none;
    }

    &__steps li:not(:last-of-type) {
        margin-bottom: 2.5rem;

    }

    &__steps li {
        line-height: 1.6;
    }
}

.extras {
    background-color: rgba(75, 152, 240, 0.1);

}


.detailed-instructions{
    & a{
        text-decoration: none;
    }
}

@media screen and (min-width: 800px) {
    
    .full-recipe {
        height: 100%;
        max-height: min-content;
        
    }
    .recipeImage-container {
        height: 300px;
        width: 450px;
        margin: 2rem auto;
    }
}