.search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(to bottom right, rgba(236, 159, 5, 1.0) 0%, rgba(236, 159, 5, 1.0) 50%, rgba(191, 49, 0, 1.0) 100%);
    // background: salmon;
    padding: .5rem;
    position: sticky;
    top: 0;
    overflow: hidden;
    height: 80px;
    max-height: 10vh;
    z-index: 50;


}

.search::after {
    content: '';
    position: absolute;
    top: 0;
    right: -5%;
    height: 100%;
    width: 50%;
    background: rgba(191, 49, 0, 0.3);
    transform: skew(25deg);
}

.search-form {
    flex: 1 1 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: .5rem;
    z-index: 5;
    // background-color: grey;    
}

.search-btn {
    border: none;
    background: #ececec;
    // background: red;
    margin-left: -3.5rem;
    cursor: pointer;


    &:focus {
        outline: none;
    }

}

.icon-search {
    height: 1.5rem;
    width: 1.5rem;
    fill: rgba(129, 129, 129, 0.2);

    &:hover {
        fill: rgba(129, 129, 129, 0.6);

    }
}

.app-name {
    color: #ececec;
    font-weight: bold;
    font-size: 1.8rem;
    margin: 0 auto;
    flex: 1 1 15%;
    text-align: center;
    text-transform: capitalize;
    // background-color: violet;
}



.icon-heart--full {
    font-size: 3rem;
    color: rgba(111 80 199 / 75%);

}



.favs {
    // background-color: green;
    flex: 1 1 10%;
    text-align: center;
    display: block;
    z-index: 5;
    display: block;
}


.favs:hover {

    .icon-heart--full{
        color: rgba(111 80 199 / 100%);
    }

    .liked-recipes {
        opacity: 1;
        right: 0;

    }
    

}

.liked-recipes {
    list-style: none;
    display: flex;
    flex-direction: column;
    position: fixed;
    // position: absolute;
    top: 7vh;
    right: -100%;
    width: 60vw;
    transition: all 1s;
    padding: 1rem;
    background-color: #fff;
    z-index: 50;
}



.favorite a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-decoration: none;
}

.favorite {
    width: 100%;
    background-color: #0600150a;
    // background-color: lightgray;
}

.favorite:not(:last-of-type) {
    margin-bottom: .2rem;
    border-bottom: 2px solid rgba(50, 50, 50, .2);
}

.search-field {
    background: #ececec;
    border-radius: 100px;
    border: none;
    width: 75%;
    transition: all .5s ease-in-out;
    padding: 1rem;
    margin-left: -1rem;

    &:focus {
        outline: none;
        width: 100%;
    }

}

.fav-name {
    flex: 1;
    font-size: 1rem;
    text-transform: uppercase;
    padding: 0 .5rem;
}

.fav-img {
    display: block;
    height: 6.5rem;
    width: 8rem;
    object-fit: cover;
}

@media screen and (min-width: 600px) {
     
        .liked-recipes {
            top: 9vh;
        }
    


}
@media screen and (min-width: 800px) {
    
    .favs:hover {
       
        .liked-recipes {
            width: 380px;
        }
    }


}