.recipes {
  background: rgba(200, 200, 200,0.3);
  position: relative;
  min-height: calc( 100vh - 80px);
}

.recipes-list {
  display: grid;
  grid-template-columns: minmax(300px, 1fr);
  grid-auto-rows: 100px;
  list-style: none;
}
.recipes-list li{
  background-color: inherit;
  margin-bottom: 1px;
  // z-index: 50;
  &:hover .recipe-link{
    background-color: rgba(255, 255, 255, 50%);
  }
}

.recipe-link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-decoration: none;
  height: 100%;
  border-bottom: 2px solid rgba(1, 1, 1, 0.2);
  background-color: rgb(255,255,255);
}

.recipe-image {
  height: 100%;
  width: 55%;
  background-color: orange;
  position: relative;
  
}


.recipe-image::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  box-shadow:inset -2px -2px 3px 5px rgba(255, 255, 255, 0.3), 
             inset  2px 2px 3px 5px rgba(50, 50, 50, 0.3);
}

.recipe-image img {
  display: block;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;  
}


.recipe-title span{
  font-size: 1.4rem;
  color: black;
}

.recipe-title {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: auto;
  text-align: right;
  padding: 0 1rem;
  text-transform: capitalize;
  
}

.title-active{
  animation: flash .5s .5s 2 normal forwards;
  transition: all .5s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

@keyframes flash {
  0%{ background-color: rgba(200, 200, 200,0.3);}
  50%{background-color: rgba(236, 159, 5, .5);}
  100%{ background-color: rgb(255,255,255);}
}

///Pagination buttons
.pagination {
  position: absolute;
  top: 33.33%;
  left: 0;
  width: 100%;
  overflow: hidden;
  display: flex;
  
}

.btn {
  height: 6rem;
  width: 8rem;
  border-radius: 50px;
  padding: 1rem;
  text-align: center;
  border: none;
  background: rgba(255, 255, 255, 0.3);
  background: rgba(114, 241, 98, 0.877);
  transition: all .3s ;
  z-index: 51;
  
}

.btn:hover span {
  color: white;
  font-size: 2rem;
  font-weight: 600;
}

.btn-prev:hover {
  transform: translateX(-45%);
}
.btn-next:hover {
  transform: translateX(45%);

}
.btn:hover {
  width: 10rem;
}

.btn span {
  font-weight: bold;
  display: block;
  color: rgba(255,255,255, 0.9);
  transition: inherit;
}

span.prev-arrow {
  transform: translatex(30%);
}

span.next-arrow {
  transform: translatex(-30%);
}

.btn-prev {
  margin-right: auto;
  transform: translateX(-50%);
}

.btn-next {
  margin-left: auto;
  transform: translateX(50%);
}

@media screen and (min-width: 600px) {
  .recipes {
    min-height: calc( 96vh - 80px);
  }
  
}

@media screen and (min-width: 800px) {
  .pagination {
    top: 20vw;
    }  

  .recipes-list {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-auto-rows: 250px;
    gap: 5rem 3rem;
    padding: 15px;
    
  }
  
  .recipe-link {
    flex-direction: column;
    border: 2px solid rgba(1, 1, 1, 0.2);
    border-radius: 4px;
    box-shadow: 0 1px 15px 2px rgba(100,100,100, .1),
    0 1px 10px 1px rgba(1, 1, 1, 0.2);
    transition: all .2s ease-out;
    overflow: hidden;
    border-radius: 5px;

    &:hover {
      // transform: scale(1.03);
      transform: scale(1.01) translateY(-10px);
      box-shadow: 0 10px 15px 1px rgba(1, 1, 1, 0.3);
    }
  }

  .recipe-image {
    width: 100%;
    height: 75%;
  }

  .recipe-title span{
    // background: orange;
    font-size: 1.7rem;
  }
  .recipe-title {
    // background-color: green;
    width: 100%;
    justify-content: center;
    // background-color: rgb(250, 245, 245);
  }
}